import React from 'react';
import { useHistory } from 'react-router';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import EmptyCart from '../../components/Cart/EmptyCart';
import Footer from '../../components/common/Footer';
import Logo from '../../components/common/Logo';
import CartMenu from '../../components/common/Menu/CartMenu';
import Payment from '../Payment'
import { cartRemoveAllItems } from '../../redux/cart/cart.action';
import { selectCartItems, selectCartItemsCount, selectCartTotal } from '../../redux/cart/cart.selector';
import './styles.css';

const Cart = ({ cartCount, cartList, cartTotal, cartRemoveAllItems }) => {
  const history = useHistory();

  function clearCart() {
    console.log(cartList)
    cartRemoveAllItems()
    console.log(cartList)
  }

  const CartListDescription = () => {
    if (cartList != null) {
      return (
        cartList.map((item) => (
          <h3 className='orders-total-description-item'>{item.name} <span className='order-description-item-price'>£{item.price}</span></h3>
        ))
      )
    } else {
      return null
    }
  }

  return (
    <>
      <div className='cart-header'>
        <button className='back-button' onClick={() => history.push('/')}>
          <i className="fa fa-arrow-left"></i>Go Back
        </button>
        <button className='back-button' onClick={clearCart}>
          <i className="fa fa-arrow-left"></i>Clear Cart
        </button>
      </div>
      {cartCount === 0 ? (
        <EmptyCart />
      ) : (
        <div className='orders'>
          <h1 className='orders-heading'>Your Orders</h1>
          <div className='orders-menu'>
            <CartMenu list={cartList} />
          </div>
          <h3 className='orders-total-description'></h3>
          <CartListDescription />
          <h3 className='orders-total-description-item'>Delivery <span className='order-description-delivery-price'>£0.00</span></h3>
          <h3 className='orders-total'>Your Total <span className='order-description-item-price'>£{cartTotal}</span></h3>
          <Payment />
        </div>
      )}
      <Footer />
    </>
  );
};

const mapStateToProps = createStructuredSelector({
  cartCount: selectCartItemsCount,
  cartList: selectCartItems,
  cartTotal: selectCartTotal,
});

const mapDispatchToProps = (dispatch) => ({
  cartRemoveAllItems: () => dispatch(cartRemoveAllItems())
});

export default connect(mapStateToProps, mapDispatchToProps)(Cart);
